import React, { Component } from 'react';
import './Login.css';

import {ToastContainer, ToastStore} from 'react-toasts';

import axios from 'axios';
import { API_URL, ERRORS } from '../../js/constants';

class Login extends Component {

    constructor(){
        super();

        this.onLogin = this.onLogin.bind(this);
        this.onRegister = this.onRegister.bind(this);
    }

    onLogin(event) {
        event.preventDefault();
        
        const data = new FormData(event.target);
        const credentials = {
            username: data.get('username'),
            password: data.get('password')
        };

        this.handleLogin(credentials);
    }

    onRegister(event) {
        event.preventDefault();
        
        const data = new FormData(event.target);
        const credentials = {
            username: data.get('username'),
            password: data.get('password')
        };

        if(credentials.password !== data.get('passwordRepeat')) return ToastStore.error(ERRORS.PASSWORDS_DONT_MATCH);

        axios.post(`${API_URL}/users`, credentials)
            .then(() => {
                this.handleLogin(credentials);
            })
            .catch(error => {
                this.handleErrors(error.response.data);
            });
    }

    handleLogin(credentials) {
        axios.post(`${API_URL}/session`, credentials)
            .then(result => {
                const user = result.data;

                localStorage.setItem('userId', user._id);
                localStorage.setItem('username', user.username);
                localStorage.setItem('accessToken', user.access_token);
                localStorage.setItem('refreshToken', user.refresh_token);

                window.location.reload();
            })
            .catch(error => {
                this.handleErrors(error.response.data);
            });
    }

    handleErrors(error) {
        if(error.errors){
            for(let err of error.errors){
                ToastStore.error(err.messages.join(', '));
            }
        }
        else ToastStore.error(error.message);
    }

    render() {
        return (
            <div>
                <div className="mdl-tabs mdl-js-tabs mdl-js-ripple-effect">
                    <div className="mdl-tabs__tab-bar">
                        <a href="#login" className="mdl-tabs__tab is-active">Login</a>
                        <a href="#register" className="mdl-tabs__tab">Register</a>
                    </div>

                    <div className="mdl-tabs__panel is-active" id="login">
                        <form className='credentials' onSubmit={this.onLogin}>
                            <div className="mdl-textfield mdl-js-textfield">
                                <input className="mdl-textfield__input" type="text" name="username" required minLength='5' />
                                <label className="mdl-textfield__label" htmlFor="username">Username</label>
                            </div>
                            <div className="mdl-textfield mdl-js-textfield">
                                <input className="mdl-textfield__input" type="password" name="password" required minLength='5' />
                                <label className="mdl-textfield__label" htmlFor="password">Password</label>
                            </div>
                            <input type="submit" className="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--accent" value="Login" />
                        </form>
                    </div>

                    <div className="mdl-tabs__panel" id="register">
                        <form className='credentials' onSubmit={this.onRegister}>
                            <div className="mdl-textfield mdl-js-textfield">
                                <input className="mdl-textfield__input" type="text" name="username" required minLength='5' />
                                <label className="mdl-textfield__label" htmlFor="username">Username</label>
                            </div>
                            <div className="mdl-textfield mdl-js-textfield">
                                <input className="mdl-textfield__input" type="password" name="password" required minLength='5' />
                                <label className="mdl-textfield__label" htmlFor="password">Password</label>
                            </div>
                            <div className="mdl-textfield mdl-js-textfield">
                                <input className="mdl-textfield__input" type="password" name="passwordRepeat" required minLength='5' />
                                <label className="mdl-textfield__label" htmlFor="passwordRepeat">Repeat password</label>
                            </div>
                            <input type="submit" className="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--accent" value="Register" />
                        </form>
                    </div>
                </div>
            
                <ToastContainer store={ToastStore} position={ToastContainer.POSITION.BOTTOM_LEFT} />
            </div>
        );
    }
}

export default Login;


