import React, { Component } from 'react';
import './Chat.css';

import { ToastContainer, ToastStore } from 'react-toasts';
import io from 'socket.io-client';
import update from 'react-addons-update';

import axios from 'axios';
import { API_URL, SOCKET_URL, MESSAGES, ERRORS, EVENTS } from '../../js/constants';

class Chat extends Component {

    constructor(props) {
        super(props);

        this.state = {
            _id: localStorage.getItem('userId'),
            username: localStorage.getItem('username'),
            accessToken: localStorage.getItem('accessToken'),
            refreshToken: localStorage.getItem('refreshToken'),

            updatedUsername: localStorage.getItem('username'),

            users: [],
            messages: [],
            
            currentRecipient: null,

            message: ''
        };

        this.onUsernameInputChange = this.onUsernameInputChange.bind(this);
        this.updateUsername = this.updateUsername.bind(this);
        this.logout = this.logout.bind(this);
        this.getUsers = this.getUsers.bind(this);
        this.openChat = this.openChat.bind(this);
        this.sendMessage = this.sendMessage.bind(this);
        this.onMessageInputChange = this.onMessageInputChange.bind(this);
        this.receiveMessage = this.receiveMessage.bind(this);
        this.onUsernameUpdated = this.onUsernameUpdated.bind(this);
        this.onNewUser = this.onNewUser.bind(this);
        this.componentDidMount = this.componentDidMount.bind(this);

        this.getUsers();
    }

    componentDidMount() {
        this.socket = io(`${SOCKET_URL}?access_token=${localStorage.getItem('accessToken')}`)
        
        this.socket.on(EVENTS.ERROR, error => {
            ToastStore.error(error);
        });

        this.socket.on(EVENTS.NEW_MESSAGE, this.receiveMessage);
        this.socket.on(EVENTS.USERNAME_UPDATED, this.onUsernameUpdated);
        this.socket.on(EVENTS.NEW_USER, this.onNewUser);
    }

    onNewUser(user) {
        console.log(user);

        this.setState(prevState => ({
            users: [ ...prevState.users, user]
        }));
    }

    onUsernameUpdated(user) {
        const { users } = this.state;

        const index = users.findIndex(u => {
            return u._id === user._id;
        })

        this.setState({
            users: update(users, {
                [index]: {
                    username: { $set: user.username }
                }
            })
        });
    }

    openChat(event) {
        const recipient = event.target.id;

        const { accessToken } = this.state;

        document.getElementById('userList').classList.remove('is-visible');
        document.getElementsByClassName('mdl-layout__obfuscator is-visible')[0].classList.remove('is-visible');

        axios.get(`${API_URL}/messages/${recipient}?limit=99`, {
            headers: {
                'x-access-token': accessToken
            }
        })
        .then(result => {
            const messages = result.data;

            this.setState({
                currentRecipient: recipient,
                messages: messages.messages.reverse()
            });

            const chatElement = document.getElementById('chat');
            chatElement.scrollTop = chatElement.scrollHeight;
        })
        .catch(error => {
            this.handleErrors(error.response);
        });
    }

    receiveMessage(newMessage) {
        const { currentRecipient } = this.state;

        if(currentRecipient !== newMessage.from) return;

        this.setState(prevState => ({
            messages:[ ...prevState.messages, newMessage]
        }));

        const chatElement = document.getElementById('chat');
        chatElement.scrollTop = chatElement.scrollHeight;
    }

    sendMessage(event) {
        event.preventDefault();

        const { currentRecipient} = this.state;

        const data = new FormData(event.target);

        const msg = data.get('message');

        if(!msg.replace(/^\s+/g, '').length) return;

        const message = {
            recipient_id: currentRecipient,
            message: msg
        };

        this.socket.emit(EVENTS.SEND_MESSAGE, message, (error, newMessage) => {
            if(error) return ToastStore.error(error);

            this.setState(prevState => ({
                message: '',
                messages:[ ...prevState.messages, newMessage]
            }));
          
            const chatElement = document.getElementById('chat');
            chatElement.scrollTop = chatElement.scrollHeight;
        });
    }

    onMessageInputChange(event) {
        this.setState({ 
            message: event.target.value,
        });
    }

    getUsers() {
        const { accessToken } = this.state;

        axios.get(`${API_URL}/users`, {
            headers: {
                'x-access-token': accessToken
            }
        })
        .then(result => {
            const users = result.data;

            this.setState({ users });
        })
        .catch(error => {
            this.handleErrors(error.response);
        });
    }

    onUsernameInputChange(event) {
        let valid = true;

        if(event.target.value.length < 5) valid = false;
        if(event.target.value.length > 20) return;

        this.setState({ 
            updatedUsername: event.target.value,
            usernameIsValid: valid
        });
    }

    updateUsername(event) {
        event.preventDefault();

        const { accessToken, updatedUsername } = this.state;

        axios.put(`${API_URL}/users`, {username: updatedUsername}, {
            headers: {
                'x-access-token': accessToken
            }
        })
        .then(result => {
            const username = result.data.username;

            localStorage.setItem('username', username);

            ToastStore.success(MESSAGES.USERNAME_UPDATED);

            this.setState({
                username: username,
                updatedUsername: username,
                usernameIsValid: false
            });
        })
        .catch(error => {
            this.handleErrors(error.response);
        });
    }

    logout() {
        const { accessToken, refreshToken } = this.state;

        axios.delete(`${API_URL}/session`, {
            headers: {
                'x-access-token': accessToken,
                'x-refresh-token': refreshToken
            }
        })
        .then(result => {
            localStorage.clear();
            this.socket.close();
            ToastStore.success(MESSAGES.LOGGING_OUT);
            setTimeout(() => {
                window.location.reload();
            }, 1000);   
        })
        .catch(error => {
            this.handleErrors(error.response);
        });
    }

    handleErrors(error) {
        if(!error) return ToastStore.error(ERRORS.SERVER_UNREACHABLE);
        if(!error.data) return ToastStore.error(ERRORS.UNKNOWN_ERROR);
        if(error.status === 403){
            localStorage.clear(); 
            window.location.reload();
        }
        else {
            ToastStore.error(error.data.message);
        }
    }

    render() {
        const { updatedUsername, usernameIsValid, users, message, messages, currentRecipient, _id, user } = this.state;

        return (
            <div className="mdl-layout mdl-js-layout mdl-layout--fixed-header">
                <header className="mdl-layout__header">
                    <div className="mdl-layout__header-row">

                        <form>
                            <div className="mdl-textfield mdl-js-textfield">
                                <input className="mdl-textfield__input" type="text" id="username" value={updatedUsername} onChange={this.onUsernameInputChange} minLength='5' required />
                                <label className="mdl-textfield__label" htmlFor="username">Username</label>
                            </div>
                            <div className="mdl-tooltip" data-mdl-for="editUsernameButton">
                                Update username
                            </div>
                            <button className="mdl-button mdl-js-button mdl-button--icon" id="editUsernameButton" disabled={!usernameIsValid} onClick={this.updateUsername}>
                                <i className="material-icons">edit</i>
                            </button>
                        </form>
                        <div className="mdl-tooltip" data-mdl-for="logoutButton">
                            Logout
                        </div>
                        <button className="mdl-button mdl-js-button mdl-button--icon" id="logoutButton" onClick={this.logout}>
                            <i className="material-icons">backspace</i>
                        </button>

                        <div className="mdl-layout-spacer">

                        </div>

                    </div>
                </header>
                <div className="mdl-layout__drawer" id="userList">
                    <span className="mdl-layout-title">Users</span>
                    <nav className="mdl-navigation">
                        {
                            users.map(user => {
                                return (<div className="mdl-navigation__link" id={user._id} key={user._id} onClick={this.openChat}>{user.username}</div>)
                            })
                        }
                    </nav>
                </div>

                    <ul className="chat" id="chat">
                        {
                            messages.map(message => {
                                if(message.from === _id){
                                    return (
                                        <li className="message from" key={message._id}>
                                            {message.message}
                                        </li>
                                    );
                                }
                                else {
                                    return (
                                        <li className="message to" key={message._id}>
                                            {message.message}
                                        </li>
                                    );
                                }
                            })
                        }
                    </ul>

                
                {
                    (() => {
                        if(currentRecipient) return (
                            <div className="footer">
                                    <form onSubmit={this.sendMessage}>
                                        <input className="message-input" type="text" id="message" name="message" autoComplete='off' value={message} onChange={this.onMessageInputChange} />
                                    </form>
                            </div>
                        );
                    })()
                    
                }
                <ToastContainer store={ToastStore} position={ToastContainer.POSITION.BOTTOM_LEFT}/>
            </div>
        );
    }
}

export default Chat;


